.fixed-navbar {
    background-color:#fff;
    position: fixed; /* Make it stick/fixed */
    top: 0; /* Stay on top */
    width: 100%; /* Full width */
    transition: top 0.3s; /* Transition effect when sliding down (and up) */
    height: 70px;
    background-attachment: fixed;
}

.listNavbar li{
  display: inline;
  margin-left: 1rem;
  color: #808080;
  
}

  .search{
    width: 65%;
    height: 40px;
    border-radius: 8px;
    border: rgb(170, 165, 165) solid 0.5px;
    background-color:#efefef; 
    padding-left: 12px;
    padding-right: 12px;
    flex: 1 1 auto;
    min-height: 0;
    min-width: 0;
    font-size: 15px;
  }
  .fas.fa-comment-dots, .fas.fa-bell, .fab.fa-adn{
    font-size: 25px;
    color: #808080;
  }

  .fab.fa-pinterest{
    font-size: 30px;
    color: #e60023;
  }

  
  