
.fatherModal{
    width: 100%;
    height: 100%;
    position:fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background: linear-gradient(rgb(238, 238, 238) 300px, rgb(255, 255, 255) 100%);
}

.childModal{
    background:#fff;
    width: 68%;
    height: 85%;
    justify-content: center;
    margin: auto;
    border-radius: 10px;
    margin-top: 50px;
}

#btnes-modal{
    
    float:right;
    margin-right: 50px;
   
}
.modal-img{
    width: 30%;
    margin:5%;
    float:left;
    position: sticky;
}

#imgModal{
    width: 340px;
    height: 450px;
    border-radius: 8px;
} 

#text{
    padding-left: 450px;
}

#btn-save, #btn-send{
    display: inline-block;
    height: 2.5rem;
    width: 6rem;
    border-radius:8px;
    margin: 0.3rem;
    margin-top: -40px;
}

#btn-save{
    background-color: #e60023;
    color:#fff;
    font-weight: bold;
}

#btn-send{
    background-color: #efefef;
    color:solid #212121;  
    font-weight: bold;
}

hr{
    width: 430px;
    margin-left: -30px;
    color: #efefef;
}

#text-comments, .fas.fa-angle-down{
    display: inline;
    padding-top:20px
}

.fas.fa-angle-down{
    margin-left: 240px;
    color:#808080;
    font-size: 22px;
    text-align: center;
}

#text-content{
    padding-top: 10px;
}

#publish{
    font-size: 10px;
}

.fas.fa-user-circle{
    font-size: 30px;
    color: rgb(163, 113, 21);
}

#menu{
margin-left: 5%;
}

.fas.fa-ellipsis-h{
    font-size: 20px;
    color:#808080;
    margin-top:40px;
}

#btnPhoto{
    border-radius: 20px;
    background-color: #efefef;
    width: 4rem;
    height: 2rem;
    color: #212121;
    font-weight: bold;
    font-size: 13px;
}

#btnComments{
    border: none;
    background-color: transparent;
    color:#3a3838;
    font-weight: bold;
    margin-left: 10px;
    font-size: 13px;
}

#addBtn{
    width: 5rem;
    height: 2.5rem;
    background-color: #efefef;
    color: #212121;
    font-weight: bold;
    border-radius: 5px;
    float: right;
    margin-right: 160px;
    margin-top: -65px;
}

#firstLine{
    margin-top: 90px;
}






