body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  -webkit-column-count: 6;
          column-count: 6;
  width: 100%;
  padding: 0.5rem;
  max-width: 100%;
  margin-top: 2%;
}
#imgCard{
    border-radius: 10px;
    margin: 10px;
   
  }
 
  
  
.fixed-navbar {
    background-color:#fff;
    position: fixed; /* Make it stick/fixed */
    top: 0; /* Stay on top */
    width: 100%; /* Full width */
    transition: top 0.3s; /* Transition effect when sliding down (and up) */
    height: 70px;
    background-attachment: fixed;
}

.listNavbar li{
  display: inline;
  margin-left: 1rem;
  color: #808080;
  
}

  .search{
    width: 65%;
    height: 40px;
    border-radius: 8px;
    border: rgb(170, 165, 165) solid 0.5px;
    background-color:#efefef; 
    padding-left: 12px;
    padding-right: 12px;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    min-height: 0;
    min-width: 0;
    font-size: 15px;
  }
  .fas.fa-comment-dots, .fas.fa-bell, .fab.fa-adn{
    font-size: 25px;
    color: #808080;
  }

  .fab.fa-pinterest{
    font-size: 30px;
    color: #e60023;
  }

  
  

.fatherModal{
    width: 100%;
    height: 100%;
    position:fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background: linear-gradient(rgb(238, 238, 238) 300px, rgb(255, 255, 255) 100%);
}

.childModal{
    background:#fff;
    width: 68%;
    height: 85%;
    -webkit-justify-content: center;
            justify-content: center;
    margin: auto;
    border-radius: 10px;
    margin-top: 50px;
}

#btnes-modal{
    
    float:right;
    margin-right: 50px;
   
}
.modal-img{
    width: 30%;
    margin:5%;
    float:left;
    position: -webkit-sticky;
    position: sticky;
}

#imgModal{
    width: 340px;
    height: 450px;
    border-radius: 8px;
} 

#text{
    padding-left: 450px;
}

#btn-save, #btn-send{
    display: inline-block;
    height: 2.5rem;
    width: 6rem;
    border-radius:8px;
    margin: 0.3rem;
    margin-top: -40px;
}

#btn-save{
    background-color: #e60023;
    color:#fff;
    font-weight: bold;
}

#btn-send{
    background-color: #efefef;
    color:solid #212121;  
    font-weight: bold;
}

hr{
    width: 430px;
    margin-left: -30px;
    color: #efefef;
}

#text-comments, .fas.fa-angle-down{
    display: inline;
    padding-top:20px
}

.fas.fa-angle-down{
    margin-left: 240px;
    color:#808080;
    font-size: 22px;
    text-align: center;
}

#text-content{
    padding-top: 10px;
}

#publish{
    font-size: 10px;
}

.fas.fa-user-circle{
    font-size: 30px;
    color: rgb(163, 113, 21);
}

#menu{
margin-left: 5%;
}

.fas.fa-ellipsis-h{
    font-size: 20px;
    color:#808080;
    margin-top:40px;
}

#btnPhoto{
    border-radius: 20px;
    background-color: #efefef;
    width: 4rem;
    height: 2rem;
    color: #212121;
    font-weight: bold;
    font-size: 13px;
}

#btnComments{
    border: none;
    background-color: transparent;
    color:#3a3838;
    font-weight: bold;
    margin-left: 10px;
    font-size: 13px;
}

#addBtn{
    width: 5rem;
    height: 2.5rem;
    background-color: #efefef;
    color: #212121;
    font-weight: bold;
    border-radius: 5px;
    float: right;
    margin-right: 160px;
    margin-top: -65px;
}

#firstLine{
    margin-top: 90px;
}







